import {genreTable} from './geners.js';

//     ID
export function genresSearch(data) {
  const arrGenres = [];
  const genre = data.map(element => {
      return genreTable.filter(el => el.id === element).map(el => el.name);
    }).join(', ');
    arrGenres.push(genre)[0];
    return arrGenres;
}